<template>
  <!-- App -->
  <div id="app">

    <!-- Statusbar -->
    <f7-statusbar></f7-statusbar>

    <!-- Main Views -->
    <f7-views>
      <f7-view id="main-view" navbar-through :dynamic-navbar="true" main>
        <!-- Navbar -->
        <f7-navbar>
         <f7-nav-center sliding>NOTPLAN</f7-nav-center>
          
        </f7-navbar>
        <!-- Pages -->
        <f7-pages>
          <f7-page>
            <f7-block-title>Notlar, planlar ve hatırlatmalar</f7-block-title>
            <f7-block inner>
              <p>Her türlü notlarınızı alabileceğiniz ve kendinize hatırlatma yapabileceğiniz uygulama. Notunuzu eklemek için bilginizi girdikten sonra Enter tuşuna veya butona basarak kayıt işlemini tamamlayabilirsiniz.</p>
            </f7-block>
            <f7-block-title>Girişinizi yapınız</f7-block-title>
            <f7-list>
              <f7-list-item>
                <f7-input v-model="minput" v-on:keypress.enter="notekle" type="text" placeholder="Notlar"/>
                <f7-button v-on:click="notekle">Ekle</f7-button>
              </f7-list-item>
              </f7-list>
              <f7-grid>
                <f7-col width="25">ID</f7-col>
                <f7-col width="65">Açıklama</f7-col>
                <f7-col width="10">Sil</f7-col>
              </f7-grid>
              <f7-grid v-for="(not, index) in liste">
                <f7-col width="25">{{not.id}}</f7-col>
                <f7-col width="65">{{not.aciklama}}</f7-col>
                <f7-col width="10"><f7-button v-on:click="degersil(index)">X</f7-button></f7-col>
              </f7-grid>
          </f7-page>
        </f7-pages>
      </f7-view>
    </f7-views>
  </div>
</template>

<script>
export default {
  props: {
    minput: ''
  },
  data () {
    return {
      id: 1004,
      liste: []
    }
  },
  methods: {
    // ekleme metodu
    notekle: function () {
      this.liste.push({ id: this.id++, aciklama: this.minput })
      this.minput = ''
    },
    // silme metodu
    degersil: function (index) { this.liste.splice(index, 1) }
  }
}
</script>


